html *{
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --main-bg-color: #e2ceb5;
  --secundary-bg-color: black;
}

body, html, #root{
  background-color: var(--main-bg-color);
}
.main{
  background-color: var(--main-bg-color);
  width: 100%;
}

.granContenedor{
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

                                                         /* HEADER */
.logo{
    width:25vh;
    height:25vh;
    background-position:center;
    background-size: contain;
    background-repeat: no-repeat;
  }
  .welcomeAndAvatar{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .welcome{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    max-width: 2em;
    text-shadow: -2px 2px 1px rgba(0,0,0,1);
    margin-top: 1vh;
    color: var(--main-bg-color);
  }
  .genericUser{
    width:80px;
    height:80px;
    background-position:center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  button.nav-link.genericUser{
    background-color: var(--main-bg-color);
  }
  .nav-link .active{
    color: var(--main-bg-color) !important;
  }
  .nav-link{
    cursor: pointer;
    color: var(--secundary-bg-color);
    text-align: center;
    width: 80px;
    padding: 1vh;
    border-radius: 0 30px ;
  }
   .navbar{
     margin-right: 2vw;
   }
  .navbar .nav-link{
    background-color: rgba(0, 0, 0, 0.322) ;
    text-shadow: 1px 0px 5px rgba(0,0,0,1) ;
    color: var(--main-bg-color) !important;
  }
  .navbar .nav-link:hover{
    text-shadow: 1px 0px 5px rgba(0,0,0,1);
    color: rgb(85, 85, 85) !important;
  }
  
  .responsiveNav{
    display: none !important;  
  }
  ul button{
    box-shadow: 2px 2px 44px -5px rgba(0,0,0,0.75);
    margin: 0 !important;
    padding: 2vh 0 !important;
    border-radius: 100px !important;
    border: 4px solid var(--secundary-bg-color) !important;
  } 

  ul.dropdown-menu.show{
    margin-top: 3vh !important;
  }
   
  .dropdown-menu{
    text-align: center !important;
    color: rgb(177, 177, 177) !important;
    background-color: rgba(0,0,0,0.75) !important;
  }
  .dropdown-item:hover{
    background-color: rgba(0,0,0,0.75) !important;
  }
  .dropdown-item{
    cursor: pointer;
    color: var(--main-bg-color) !important;
  }
  .active{
    box-shadow: 15px 13px 44px 8px rgba(0,0,0,0.75);
    color: var(--main-bg-color) !important;
    background-color: var(--secundary-bg-color) !important;
  }

  .header{
    position: absolute;
    z-index: 2;
    top: -3%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 1vh auto;
    padding: 0 2vw;
  }
  .headerDer{
    margin: 2vh 0;
    display: flex !important;
  }
                                                          /* SIGN UP / LOG IN */
  .backgroundSign{
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #46464656;
    background-blend-mode: soft-light;
    z-index: 1;
    width:100%;
    height: 150vh;
    background-position:center;
    background-size:cover;
    background-repeat: no-repeat;
  }
  .form-control{
    display: inline !important;
  }
  .form-control.is-valid{
    border: 3px solid #28a745;
    padding-right: 0 !important;
    transform: none !important;
  }
  .form-control.is-invalid{
    border: 3px solid #dc3545;
    padding-right: 0 !important;
  }

  form div svg{
    color: rgba(0, 0, 0, 0.529);
    width: 30px;
    height: 25px
  }

  .aclaration{
    margin: 0 0 0 3vw !important;
    padding: 0;
    height: 0;
    font-size: 0.8vw;
    width: 30vw;
  }
  .googleButton{
    display: flex;
    align-items: center;
    border: none;
    transition: box-shadow 0.5s, background-color 0.5s;
    box-shadow: 0px 0px 15px 1px rgba(0,0,0,0.85);
    border-radius: 0 50px !important ;  
    font-size: 15px;
    width: 15vw;
    height: 50px;
    padding: 1em;   
    border-radius: 0 30px;
    color: var(--main-bg-color);
    background-color: var(--secundary-bg-color);
  }  
  .googleButton svg{
    margin-left: 1vw !important;
  }
  .formCard h2{
    margin: 1vw;
  }
  .formCard h4{
    margin: 1vw;
  }
  
  .formCard{
    z-index: 5 !important;
    box-shadow: 2px 2px 30px -5px rgba(0,0,0,0.75);
    border-radius: 30px;
    position: relative;
    top:0vh;
    width: 45vw;
    height: auto;
    background-color: var(--main-bg-color);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .logIn{
    top:-16vh;
  }

  .formCard form input{
    margin:  1.5vh 0 0 1vw !important;
    outline: none;
    box-shadow: 2px 2px 20px -5px rgba(0,0,0,0.75);
    text-align: center;
    border-radius: 0 15px;
    height: 5vh;
    width: 30vw;
  }
  .formCard *{
    text-align: center;
    margin: 2vh auto;
  }
  
  form *{
    cursor: pointer;
    border: none;
    margin: 1vh 0 !important;
  }
  form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .country{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  select{    
    margin:  0 0 0 1vw !important;
    box-shadow: 2px 2px 20px -5px rgba(0,0,0,0.75);
    border-radius: 0 15px;
    height: 5vh;
    width: 30vw;
    max-width: 30vw;
    text-align-last:center;
  }

  .submitYGoogle{
    display: flex;
    flex-direction: row;
    justify-content: space-between !important;
    align-items: center;
  }
  .submit{
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    transition: box-shadow 0.5s, background-color 0.5s;
    box-shadow: 0px 0px 15px 1px rgba(0,0,0,0.85);
    margin: 4vh  !important;
    color: var(--main-bg-color) !important;
    background-color: var(--secundary-bg-color);
    border-radius: 0 50px !important ;  
    width: 15vw;
    height: 50px;
    font-size: 15px;
    padding: 1em;
  }
  .submit:hover{
    box-shadow: 0px 0px 10px 3px rgba(0,0,0,0.85);
    background-color: var(--main-bg-color);
    color: var(--secundary-bg-color) !important;
    transition: box-shadow 0.5s, background-color 0.5s;
  }


                                                           /* HOME */
 .hero{
    background-color: #46464646;
    background-blend-mode: soft-light;
    border-bottom: 30px solid var(--secundary-bg-color);
    border-radius: 0  0 50px 50px ;
    z-index: 1;
    width:100%;
    height: 120vh;
    background-position:center;
    background-size:cover;
    background-repeat: no-repeat;
    box-shadow: 0px 0px 44px 8px rgba(0,0,0,1);
  }  
  .hero h1{
    text-shadow: 1px 0px 10px rgba(0,0,0,1);
    font-size: 2vw;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 30px;
    border-radius: 0 50px ;
    color: var(--main-bg-color);
    width: 50%;
    position: relative;
    top: 18%;
    left: 25% ; 
  }
  .callToAction img{
    border: 0px !important;
  } 
  .imgCallToAction{
    width:500px;
    height:500px;
    background-position:center;
    background-size: contain;
    background-repeat: no-repeat;
  }  
  .callToAction{
    background-color: var(--main-bg-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    text-align: center;
  } 
  .textoYBoton{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  
  }
  .btnCta:hover{
    box-shadow: 0px 0px 10px 3px rgba(0,0,0,0.85);
    background-color: var(--main-bg-color);
    color: var(--secundary-bg-color) !important;
    transition: box-shadow 0.5s, background-color 0.5s;
  }
  .btnCta{
    border: none;
    transition: box-shadow 0.5s, background-color 0.5s;
    box-shadow: 0px 0px 15px 1px rgba(0,0,0,0.85);
    margin-top: 2vh;
    color: var(--main-bg-color) !important;
    background-color: var(--secundary-bg-color);
    border-radius: 0 50px !important ;  
    width: 150px;
    font-size: 20px;
    padding: 1em;
  }
                                                       /* CAROUSEL */

  .carousel-indicators .active{    
    background-color: var(--main-bg-color) !important;
  }
  .carousel{
    background-color: var(--secundary-bg-color);
    max-width: 100% !important;
    min-width: 100% !important;
    max-height: 90vh;
    min-height: 90vh;
    overflow: hidden;
  }
  .sr-only{
    display: none;
  }
  .carouselTitle{
    box-shadow: 0px 0px 44px 8px rgba(0,0,0,1);
    margin: 0;
    padding: 3vh 0 0 0;
    color: var(--main-bg-color);
    background-color: var(--secundary-bg-color) !important;
    text-align: center;
  }
  .carousel-control-prev{
    width: 8% !important;
    opacity: 1 !important;
    background-color: var(--secundary-bg-color);
  }
  .carousel-control-next{
    width: 10% !important;
    opacity: 1 !important;
    background-color: var(--secundary-bg-color);
  }
  .carousel-img{
    transition: box-shadow 0.7s;
    box-shadow: 0px 0px 35px -20px rgba(255, 255, 255, 0.75);
    border-radius: 30px;
    width: 40%;
    height: 30vh;
    background-position:center;
    background-size:cover; 
    background-repeat: no-repeat;
  }
  .carousel-img:hover{
    background-color: #464646a3;
    background-blend-mode: soft-light;
    box-shadow: 0px 0px 30px -10px rgba(255, 255, 255, 0.75);
    cursor: pointer;
    transition: background-color 0.7s, box-shadow 0.7s;
  }  
  .carousel-img .cityName{
    text-shadow: 1px 1px 5px rgba(0,0,0,1) !important;
    color: var(--main-bg-color);
    border-radius: 30px;
    background-color: rgba(0,0,0,0.4);
    text-align: center;
  }
  .carousel-slides{
    min-width: 100%;
    max-width: 100%;
    position: relative;
    left: 7vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  } 
  .carousel-item{
    max-width: 80vw;
    min-width: 80vw;
    max-height: 80vh;  
    min-height: 80vh;
  }  
  
  .carousel-inner *{
    margin: 1.5em 1em 2em 1em;
  }  

                                                        /* CITIES */

#heroAvion{
    background-color: #46464656;
    background-blend-mode: soft-light;
    border-bottom: 30px solid var(--secundary-bg-color);
    border-radius: 0  0 50px 50px ;
    z-index: 1;
    width:100%;
    height: 50vh;
    background-position:center;
    background-size:cover;
    background-repeat: no-repeat;
    box-shadow: 0px 0px 44px 8px rgba(0,0,0,0.6);
  }
  .tituloCities h1{
    font-size: 3vw;
  }

  .tituloCities *{
    margin: 2vh 4vw;
    text-align: center;
  }
  .tituloCities{
    display: flex;
    justify-content: center;
    align-items: center;
  }  
  .avionH1Cities{
    z-index: 1;
    min-width: 50px;
    width:13vw;
    background-position:center;
    background-size:cover;
    background-repeat: no-repeat;
  } 
  .finder:focus{
    outline: none ;
  }
  .finder{
    font-size: 30px;
    padding:0;
    border-bottom: 5px solid var(--secundary-bg-color) !important;
    text-align: center;
    width: 80vw;
    border: none;
    background-color: transparent;
  }  
  .cityBanners{
    box-shadow: 0px 0px 44px 8px rgba(0,0,0,0.6);
    width: 100%;
    background-color: var(--secundary-bg-color);
    margin: 5vh auto;
    padding: 7vh 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .cityBanners a {
    text-decoration: none !important;
  }
  .cityBanner{
    background-color: transparent;
    background-blend-mode: soft-light;
    margin: 2vh 0;
    border-radius: 50px;
    /* border-radius: 150px 1000px 1000px 150px; */
    width: 80vw;
    height: 40vh;
    transition: transform 1s, background-color 0.5s; 
    background-position:center;
    background-size:cover; 
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
 
  .cityBanner:hover {  
    transform: scale(1.08);
    background-color: #494949;
    background-blend-mode: soft-light;
    transition: transform 1s, background-color 0.5s; 
  }
  .parrafoBanner{
    margin-top: 0vh;
    text-align: justify;
    width: 50vw;
    font-size: 1vw;
    position: relative;
    color: var(--main-bg-color);
    opacity: 0;
    transition: opacity 0.7s;
  }
  .cityBanner:hover > .parrafoBanner{  
    font-size: 1vw;
    opacity: 1;
    transition: opacity 0.7s;
  }
  .cityBanner .cityName{
    color: var(--main-bg-color);
    width: 100%;
    background-color:#00000079;
    transition: background-color 0.5s;
    text-align: center;
  }
  .cityBanner:hover .cityName{
    background-color: #00000000;
    transition: background-color 0.5s;

  }
  .notFound{
    margin: 2vh 0;
    border-radius: 30px;
    width: 80vw;
    height: 40vh;
    background-position:center;
    background-size:cover; 
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .notFound .cityName{
    color: var(--main-bg-color);
    width: 100%;
    background-color:#00000079;
    transition: background-color 0.5s;
    text-align: center;
  }

  .btnHomeEnCities:hover{
    background-color: rgb(0, 0, 0) ;
    color: var(--main-bg-color) !important;
  }
  .btnHomeEnCities{
    border-radius: 0 50px !important ;
    border: 1px solid rgb(255, 255, 255);
    background-color: var(--main-bg-color);
    width: 300px;
    font-size: 20px;
    margin: 5vh 5vw;
    margin-bottom: 10vh;
    padding: 1em;
  }

                                                      /* ITINERARY */


  .itineraryTitle{
    margin: 2vh 0;
    text-align: center;
    font-size: 45px !important;
    color: var(--secundary-bg-color);
    text-shadow: 1px 0px 5px rgb(139, 139, 139) ;
  }
  div.cityBanners.divErrorBanner.notFoundItineraries{
    margin: 0;
    padding: 0;
  }
  .itineraryBanners{
    box-shadow: 0px 0px 44px 8px rgba(0,0,0,0.6);
    width: 100%;
    background-color: var(--secundary-bg-color);
    margin: 2vh auto;
    padding-top: 7vh 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .itineraryBanner{
    background-color: #4949499e;
    background-blend-mode: soft-light;
    margin: 2vh auto;
    border-radius: 30px;
    width: 80vw;
    height: auto ;
    transition: background-color 0.5s, box-shadow 0.5s, transform 0.5s;
    background-position:center;
    background-size:cover; 
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  .itineraryBanner:hover{
    background-color: #494949c5;
    background-blend-mode: soft-light;
    box-shadow: 0px 0px 20px -10px rgba(255, 255, 255, 0.925);
    transition: background-color 0.5s, box-shadow 0.5s;
  }

  .authorDiv{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .avatarAuthor{
    border: 3px solid #4949499e !important;
    border-radius: 100px;
    width: 120px;
    height: 120px;
    background-position:top;
    background-size:cover; 
    background-repeat: no-repeat;
  }
  .likes{
    margin-bottom: 10px;
    font-size: 18px !important;
    text-align: center;
  }

  .divPopover *{
    margin: 0 5px;
  }
  .divPopover{
    margin: 5px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    min-width: 15vw;
  }

  .itineraryObservations{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .itineraryObservations *{
    font-size: 1.2em;
    margin: 1vh 1vw;
  }
  .empty{
    cursor: pointer;
    width: 25px;
    color: red;
    transition: transform 1s;
    transform:scale(1);
  }
  .filled{
    cursor: pointer;
    width: 25px;
    color: red;
    transition: transform 1s;
    transform:scale(1.3);
  }

  .btnReadMore:hover{
    box-shadow: 0px 0px 10px 3px rgba(0,0,0,0.85);
    background-color: var(--main-bg-color);
    color: var(--secundary-bg-color) !important;
    transition: box-shadow 0.5s, background-color 0.5s;
  }
  .btnReadMore{
    border: none;
    transition: box-shadow 0.5s, background-color 0.5s;
    box-shadow: 0px 0px 15px 1px rgba(0,0,0,0.85);
    margin: 2vh 0;
    color: var(--main-bg-color) !important;
    background-color: var(--secundary-bg-color);
    border-radius: 0 50px !important ;  
    width: 150px;
    font-size: 20px;
    padding: 0.5em;
  }

  .hidden {
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: height 0.5s;
  }
  .showMore{
    visibility: visible;
    opacity: 1;
    height: 90vh;
    transition: height 0.5s, opacity 2.5s;
  }
  .containerShowMore{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .commentariesTittle{
    text-align: center;
    color: black;
    margin: 2vh 0 !important;
  }
  .activityBanner{
    box-shadow: 2px 10px 20px 1px rgba(0,0,0,0.85);
    width: 26vw;
    height: 30vh;
    margin: 3vh 2vw !important;
    background-color: #4949499e;
    background-blend-mode: soft-light;
    margin: 10vh 0;
    border-radius: 30px;
    min-width: 26vw;
    background-position:center;
    background-size:cover; 
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
  } 
  .activityName{
    font-size: 18px;
    width: 100%;
    text-shadow: 1px 1px 5px rgba(0,0,0,1) !important;
    color: var(--main-bg-color);
    border-radius: 0 0 15px 15px;
    background-color: rgba(0,0,0,0.4);
    text-align: center;
  }
  

  ::-webkit-scrollbar {   
    z-index: 10 !important; 
    height: 20px;
  }
  ::-webkit-scrollbar-track{
    background: var(--main-bg-color) ;
    border-radius: 50px;
  }
  ::-webkit-scrollbar-thumb {
    background-color: rgb(29, 29, 29);
    border-radius: 50px;
  }
  ::-webkit-scrollbar-thumb:hover {
    background-color: rgb(40, 40, 40);
  }
  .divActivities{
    /* border: 3px solid var(--secundary-bg-color); */
    width: 70vw;
    max-width: 70vw;
    min-width: 70vw;
    display: flex;
    justify-content:space-between;
    align-items: center;
    overflow-x: auto;
  }
  .historyEmptyComments{
    padding-bottom: 50px;
    border-radius: 15px;
    background-color: var(--main-bg-color);
    width: 70vw;
    height: 40vh;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    align-items: center ;
    justify-content: center;
  }
  .arrowDownward{
    font-size: 4vw;
  }
  .historyComments{
    box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.6);
    padding-bottom: 50px;
    border-radius: 15px;
    background-color: var(--main-bg-color);
    width: 70vw;
    height: 40vh;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start ;
    justify-content: flex-start;
  }

  .emptyCommentariesBox{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .commentaries{
    
    display: flex;
    flex-direction: column;
    align-items: flex-start ;
    justify-content: flex-end;
  }
  .editCommentInput{
    outline: none;
    text-indent: 20px;
    margin-left: 3vw;
    background-color:var(--main-bg-color);
    z-index: 3;
    border-radius: 15px  !important;
    border: 1px solid var(--secundary-bg-color);
    background-color:var(--main-bg-color);
    width: 56vw;
    height: 35px;
  }
  .commentInput{
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.6);
    outline: none;
    text-indent: 20px ;
    margin-left: 3vw;
    background-color:var(--main-bg-color);
    z-index: 3;
    border-radius: 15px  !important;
    border: 1px solid var(--secundary-bg-color);
    background-color:var(--main-bg-color);
    width: 100%;
    height: 35px;
  }
  .divEditCommentInput{
    background-color: #49494900 !important;;
    border-radius: 15px;
    display: flex;
    flex-direction: row;
    justify-content: center !important;
    align-items: center !important;
    background-color:var(--main-bg-color);
    z-index: 3;
  }
  .divInputComment{    
    border-radius: 0 0 15px 15px;
    width: 68.5vw;
    display: flex;
    flex-direction: row;
    justify-content: center !important;
    align-items: center !important;
    height: 50px;
    background-color:var(--main-bg-color);
    z-index: 3;
    padding: 0;
    margin: -50px 0;
  }
  .commentProfileImg{
    align-self: center;
    border: 3px solid #4949499e !important;
    border-radius: 100px;
    width: 40px;
    min-width: 40px;
    min-height: 40px;
    height: 40px;
    background-position:top;
    background-size:cover; 
    background-repeat: no-repeat;
  }
  .nameAndComment{
    color: black;
    max-width: 60vw;
    margin-left: 8px;
    display: flex;
    flex-direction: column;
    align-content: flex-start;
    justify-content:center ;
  }
  .nameAndComment *{
    font-size: 15px;
    margin: 0;
  }
  .nameAndComment p{
    padding: 2px 0;
    font-weight: bold;
    color: black;
    max-width: 40vw;
  }
  .nameAndComment h4{
    padding: 5px 1vw;
  }
  .commentContent{
    border-radius: 15px;
    max-width: 60vw;
    display: flex;
    align-content: center ;
    justify-content:flex-start;
  }
  .userComment{
    max-width: 50vw !important;
  }
  .comment{
    box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.6);
    border-radius: 30px;
    width: 99%;
    background-color: #49494929 !important;
    margin: 0.5vh ;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .comment:hover{
    background-color: #1a1a1a3c !important;
  }

  .comment:hover .optionIcons{
    opacity: 1;
    transition: opacity 0.5s;
  }
  .optionIcons{
    cursor: pointer;
    border-radius: 100px;
    opacity: 0;
    font-size: 25px;
    min-width: 25px;
    transition: opacity 0.5s, background-color 0.5s !important;
  }
  .optionIcons:hover{
    border-radius: 100px;
    background-color: rgba(255, 255, 255, 0.309);
    transition: background-color 0.5s !important;
  }
  .sendComment{
    cursor: pointer;
    z-index: 5;
    position: relative;
    left: 0em;
    font-size: 40px;
    min-width: 20px;
  }
  .iconsEditComment{
    cursor: pointer;
    z-index: 5;
    position: relative;
    left: 0em;
    font-size: 30px;
    min-width: 20px;
  }

                                                       /* FOOTER */

.footer{
    background-color: var(--main-bg-color);
    box-shadow: 15px 13px 44px 8px rgba(0,0,0,0.75);
    color: var(--secundary-bg-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2vh 1vw !important;
    width: 10%;
    text-align: center;
    width : 100%;
  }
  .footer .nav-link:hover{
    color: var(--main-bg-color) !important;
    background-color: var(--secundary-bg-color);
  }
  .footer .nav-link{
    box-shadow: 15px 13px 44px 8px rgba(0,0,0,0.2);
    text-align: center;
    width: 80px;
    padding: 1vh;
    border-radius: 0 30px ;
  }
  .icons a svg:hover *{
    background-color: var(--secundary-bg-color) !important;
    color: rgba(0, 0, 0, 0.404);
    transition: 0.5s;
  }
  .icons{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
  }
  .icons *{
    transition: 0.5s;
    cursor: pointer;
    color: var(--secundary-bg-color);
    margin: 0 2vw !important;
  }
  .icons a svg{
    transition: 1s;
    margin: 0 !important;
    padding: 0;
  }
  
  .navFooter *{
    border-bottom: 1px solid rgba(0, 0, 0, 0.452);
  }
  
  .mapa{
    text-decoration: underline;
    color: var(--secundary-bg-color);
  }

